.api-main{
  padding: 21.25em 0 18.125em;
  &:before{
    content: '';
    position: absolute;
    inset: 0;
    background-image: url(../img/api-img.svg);
    background-size: auto 100%;
    background-repeat: no-repeat;
    background-position: right top;
  }
  &__items{
    margin-top: 2.5em;
    @include grid100gap(1.25em);
  }
  &__item{
    display: flex;
    align-items: center;
    &:before{
      content: '';
      display: block;
      flex-shrink: 0;
      width: 2.5em;
      height: 2.5em;
      background-image: url(../img/check.svg);
      @include bgImgSize(100%);
      margin-right: 1.25em;
    }
  }
  &__nav{
    margin-top: 2.5em;
    display: flex;
  }
  &__wrap{
    max-width: calc(100% - 30em);
  }
  &__content{
    position: relative;
    z-index: 2;
  }
  .main__info{
    position: absolute;
    right: 0;
    bottom: 0;
  }
}

.what2{
  @media screen and (min-width: 993px){
    .what-card__info{
      max-width: 14.6875em;
    }
  }
}

.pricing2{
  @media screen and (min-width: 993px){
    .grid-column3{
      grid-template-columns: repeat(2,calc(50% - .3125em));
      max-width: 63.125em;
      margin: 0 auto;
    }
    .price-card{
      background: linear-gradient(163.19deg, #FBFBFF 0%, #F7F7FF 115.99%), linear-gradient(154.6deg, #FBFBFF 0%, #EEEEFF 95.29%);
      &:before{
        width: 18.75em;
        height: 5em;
      }
      &__title{
        font-size: 1.5em;
      }
      &__price{
        margin-left: 2.5em;
        &-num{
          font-size: 3.75em;
        }
      }
    }
  }
}

.consult{
  margin-top: 3.75em;
  &__inner{
    padding: 1.25em;
    background-image: url(../img/consult-bg.svg);
    background-size: cover;
    background-position: center;
    border-radius: 1.25em;
    color: #FFFFFF;
    display: grid;
    grid-template-columns: calc(100% - 33em) 31.25em;
    justify-content: space-between;
  }
  .block-info{
    text-align: left;
    margin: 0;
    &__desc{
      margin-left: 0;
      max-width: 31.25em;
    }
  }
  &__content{
    padding: 1.875em;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}

.consult-hint{
  margin: 2em 0;
  @include grid100gap(.625em);
  &__link{
    display: flex;
    align-items: center;
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .6875em;
    }
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    @include center;
    border: 1px solid #445673;
    border-radius: 50%;
    width: 4.375em;
    height: 4.375em;
    margin-right: 1.25em;
    &:hover{
      border-color: #fff;
    }
    svg{
      width: 1.875em;
      height: auto;
    }
  }
}

.consult-form{
  background-color: #fff;
  padding: 1.875em;
  border-radius: .625em;
  border: 1px solid #EBEDF1;
  &__fields{
    @include grid100gap(1.25em);
  }
  &__btn{
    width: 100%;
  }
}

.info{
  padding: 3.75em 0;
  &__inner{
    display: flex;
    align-items: center;
    padding: 3.125em;
    min-height: 27em;
    background-image: url(../img/info-bg.svg);
    background-size: cover;
    background-position: center;
    border-radius: 1.25em;
    color: #FFFFFF;
    position: relative;
  }
  &__text{
    max-width: 31.5em;
  }
  &__img{
    position: absolute;
    width: 33.5em;
    display: block;
    top: -11.25em;
    right: 6.5em;
  }
}

.info2{
  @media screen and (min-width: 993px){
    .info{
      &__inner{
        padding-right: 4.25em;
      }
      &__text{
        margin-left: auto;
      }
      &__img{
        right: unset;
        left: 8.125em;
        top: -3.25em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .api-main{
    padding: 6em 0 1.5em;
    &:before{
      display: none;
    }
    .main__info{
      position: static;
    }
    &__wrap{
      max-width: unset;
    }
    &__items{
      margin-top: 1.5em;
      gap: 1em;
    }
    &__item{
      &:before{
        width: 1.5em;
        height: 1.5em;
        margin-right: .5em;
      }
    }
  }

  .consult{
    margin: 1em 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 1.5em;
      padding: 0;
      background: transparent;
    }
    &__content{
      padding: 1.25em;
      background: $black;
      border-radius: 1.25em;
    }
  }

  .socials{
    font-size: .8em;
  }

  .consult-form{
    padding: 1.25em;
    box-shadow: 0 1.875em 3.125em rgba($black,.25);
  }

  .info{
    padding: 1em 0;
    &__inner{
      min-height: unset;
      padding: 1em;
      display: flex;
      flex-direction: column;
      text-align: center;
    }
    &__text{
      margin: 0 auto;
    }
    &__img{
      position: static;
      width: 70%;
      max-width: 30em;
      margin: 0 auto 1em;
    }
  }
}
