@import 'vars';
html {
    width: 100%;
    font-size: 1600px;
    @media screen and (max-width: 1600px){
        font-size: 100vw;
    }
    &.fixed-body {
        overflow-y: hidden;
    }
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.page {
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    color: $black;
    background-color: $lightBg;
    font-size: 1%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.container {
    max-width: 1600px;
    width: 100%;
    margin: 0 auto;
    padding: 0 2.5em;
}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width: 992px){
    .content{
        padding: 0;
    }

    .page{
        font-size: 2.08333%;
    }

    .container{
        padding: 0 1.25em;
    }
}


@media screen and (max-width: 576px){
    .page{
        font-size: 5%;
    }

    .container{
        padding: 0 .625em;
    }
}

