.main,
.api-main{
  background-image: url(../img/main-bg.svg);
  background-size: cover;
  color: #FFFFFF;
  position: relative;
  overflow: hidden;
}

.main{
  padding: 19.375em 0 6.25em;
  &__content{
    max-width: 56.25em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__info{
    margin-top: 1.6875em;
    max-width: 25.6875em;
    padding: 1.25em 1.875em;
    background: rgba(255, 255, 255, 0.01);
    border-bottom: 1px solid #646294;
    box-shadow: inset 0 .25em 3.125em rgba(255, 255, 255, 0.05);
    backdrop-filter: blur(25px);
    border-radius: 1.25em;
  }
  &__nav{
    margin-top: 3.125em;
    display: flex;
  }
  &__img{
    position: absolute;
    right: 0;
    bottom: -9em;
    width: 58.3125em;
    display: block;
  }
}

.advantages{
  display: flex;
  margin-top: 10em;
}

.advantage-item{
  font-feature-settings: 'pnum' on, 'lnum' on;
  padding: .4375em 0 0 1.25em;
  position: relative;
  @include lastM(0 3.125em 0 0);
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: .875em;
    height: .875em;
    background-image: url(../img/check.svg);
    @include bgImgSize(100%);
  }
}

.what{
  padding: 3.75em 0;
  margin-top: 3.75em;
}

.block-info{
  text-align: center;
  @include grid100gap(1.25em);
  margin-bottom: 2.5em;
  &__desc{
    margin: 0 auto;
    max-width: 42.86em;
  }
}

.what-card{
  background: #FFFFFF;
  border-radius: 1.25em;
  padding: 2em 2.5em;
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  &__icon{
    height: 8.125em;
    img{
      height: 100%;
      width: auto;
    }
  }
  &__info{
    margin-top: 1.875em;
    max-width: 22.5em;
    @include grid100gap(.625em);
  }
}

.location{
  padding: 3.75em 0;
  &__grid{
    padding: 1.25em 1.875em;
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - .3125em * 5 / 6));
    gap: .3125em;
  }
  &:not(._all){
    .location-card:nth-child(12)~.location-card{
      display: none;
    }
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1.25em;
  }

}

.location-card{
  padding: .5em 1.25em;
  min-height: 3.75em;
  display: flex;
  align-items: center;
  border: 1px solid #EBE5F3;
  border-radius: .625em;
  &__img{
    width: 2.5em;
    flex-shrink: 0;
    margin-right: 1.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    font-weight: 700;
    color: $gray2;
  }
}

.map{
  margin: 2.125em auto 0;
  width: 73.3125em;
  max-width: 100%;
  svg{
    width: 100%;
    height: auto;
  }
}

.animate-point{
  &._blue{
    animation: 5s ease-in-out infinite animateBlue;
  }
  &._violet{
    animation: 5s ease-in-out infinite animateViolet;
  }
  &._red{
    animation: 5s ease-in-out infinite animateRed;
  }
  &._orange{
    animation: 5s ease-in-out infinite animateOrange;
  }
}

@keyframes animateBlue {
  0%   { fill: #D7E5F2; }
  50%  { fill: #392DCA; }
  100% { fill: #D7E5F2; }
}

@keyframes animateViolet {
  0%   { fill: #D7E5F2; }
  50%  { fill: #ACA7E9; }
  100% { fill: #D7E5F2; }
}

@keyframes animateRed {
  0%   { fill: #D7E5F2; }
  50%  { fill: #E23A45; }
  100% { fill: #D7E5F2; }
}

@keyframes animateOrange {
  0%   { fill: #D7E5F2; }
  50%  { fill: #FF881B; }
  100% { fill: #D7E5F2; }
}

.pricing,
.speed{
  padding: 3.75em 0;
}

.speed-card{
  &__img{
    position: relative;
    padding-bottom: 1.25em;
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      width: calc(100% - 3.125em);
      height: 12.5em;
      max-height: 100%;
      background: $gradient;
      border-radius: 1.25em;
    }
    img{
      width: 100%;
      display: block;
      border-radius: 1.25em;
      position: relative;
      z-index: 2;
    }
  }
}

.pricing{
  .block-info{
    position: relative;
    z-index: 2;
  }
}

.price-card{
  background: linear-gradient(163.19deg, #FBFBFF 0%, #F7F7FF 115.99%);
  padding: 3.75em 1.875em;
  position: relative;
  border-radius: 1.25em;
  display: flex;
  flex-direction: column;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 6.25em;
    height: 5em;
    border-radius: 31.25em;
    background: $lightBg;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1.25em;
    padding-bottom: 1.25em;
    border-bottom: 1px solid #E1E1F0;
  }
  &__title{
    font-weight: 700;
    font-size: 1.125em;
    line-height: 150%;
  }
  &__price{
    font-feature-settings: 'pnum' on, 'lnum' on;
    font-weight: 700;
    &-num{
      font-size: 2.5em;
      line-height: 150%;
      text-align: right;
    }
    &-badge{
      display: flex;
      align-items: center;
      padding: 0 1.25em;
      white-space: nowrap;
      border-radius: 31.25em;
      background: $gradient;
      color: #fff;
      height: 1.75em;
      span{
        font-size: .75em;
      }
    }
  }
  &__desc{
    max-width: 24.25em;
  }
  &__items{
    @include grid100gap(1.25em);
    margin-top: 1.25em;
  }
  &__item{
    padding-left: 2em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: .125em;
      left: 0;
      width: 1.375em;
      height: 1.375em;
      background-image: url(../img/check2.svg);
      @include bgImgSize(100%);
    }
    span{
      font-size: 1em;
      line-height: 150%;
    }
  }
  &__nav{
    width: 100%;
    margin-top: auto;
    padding-top: 1.25em;
  }
  &__btn{
    width: 100%;
    .m-btn-primary__title{
      padding: 0;
    }
  }
  &__bottom{
    padding: .625em 1.875em;
    margin-top: 1.25em;
  }
}

.contacts{
  margin-top: 3.75em;
  padding: 7.5em 0;
  color: #fff;
  background-image: url(../img/contacts-bg.svg);
  background-size: cover;
  background-position: center;
  &__grid{
    margin: 0 auto;
    max-width: 70.9375em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 1.25em;
  }
}

.contact-card{
  color: $black;
  border-radius: .625em;
  overflow: hidden;
  height: 9.375em;
  display: flex;
  align-items: center;
  background-image: url(../img/contact-bg.svg);
  background-size: auto 100%;
  background-repeat: no-repeat;
  padding: 1em 2em 1em 5.75em;
  font-weight: 500;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 50%;
    height: 100%;
    background-color: #fff;
    border-radius: inherit;
  }
  &__content{
    position: relative;
    z-index: 2;
  }
  &__title{
    color: #9EA8B3;
  }
  &__desc{
    font-size: 1.375em;
    line-height: 140%;
  }
  &__icon{
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 3.875em;
    height: 2.9375em;
    border-radius: .625em 31.25em 31.25em .625em;
    background: $gradient;
    @include center;
    img{
      width: 1.5em;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .main,
  .api-main{
    &__info{
      margin-top: 1em;
    }
    &__nav{
      margin-top: 1.5em;
    }
  }

  .main{
    padding: 6em 0 1.5em;
    &__img{
      display: none;
    }
    .text50{
      max-width: 20em;
    }
  }

  .advantages{
    margin-top: 2.5em;
  }

  .what{
    padding: 1em 0;
    margin-top: 1em;
  }

  .block-info{
    margin-bottom: 1.5em;
    gap: 1em;
  }

  .what-card{
    padding: 1.5em 1em;
    &__icon{
      height: 6.25em;
    }
    &__info{
      margin-top: 1.25em;
    }
  }

  .location{
    padding: 1em 0;
    &__grid{
      padding: 0;
      grid-template-columns: repeat(3,calc(100% / 3 - .3125em * 2 / 3));
    }
  }

  .pricing,
  .speed{
    padding: 1em 0;
  }

  .pricing{
    .grid-column5{
      row-gap: 2em;
    }
  }

  .price-card{
    &:before{
      height: 4em;
    }
  }

  .contacts{
    margin-top: 1em;
    padding: 2em 0;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__nav{
      width: 100%;
    }
    &__info{
      padding: .75em 1em;
    }
  }

  .advantages{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.25em;
  }

  .advantage-item{
    @include lastM(0);
  }

  .location{
    padding: 1em 0;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .3125em / 2));
    }
  }

  .location-card{
    padding: .25em .75em;
    min-height: 2.5em;
    border-radius: .5em;
    &__img{
      width: 1.875em;
      margin-right: .5em;
    }
    &__title{
      font-size: .75em;
    }
  }

  .price-card{
    padding-left: 1.25em;
    padding-right: 1.25em;
    padding-bottom: 1.25em;
  }

  .contacts{
    &__grid{
      grid-template-columns: 100%;
      gap: 1em;
    }
  }

  .contact-card{
    font-size: .75em;
  }
}
