.footer{
  padding: 2.5em 0 3em;
  .menu{
    border-color: $black;
    //overflow: hidden;
    &__info{
      border-color: $black;
      padding: 0 0 0 1.875em;
      width: 56.8em;
    }
    &__links2{
      height: 7.375em;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      padding: 1.125em 1.875em;
      border-left: 2px solid $black;
      margin-left: auto;
    }
    &__link2{
      font-size: .875em;
      line-height: 140%;
      font-weight: 600;
      letter-spacing: 0.05em;
      text-transform: uppercase;
    }
  }

  .menu-nav{
    &__row,
    &__link{
      border-color: $black;
    }
  }

  .menu-auth{
    width: 9.75em;
    position: relative;
    z-index: 2;
    margin-left: -2px;
    color: #fff;
    background: $gradient;
    border-bottom-right-radius: .625em;
  }

  .lang{
    &__hidden{
      top: unset;
      bottom: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    .menu{
      &__info{
        padding: 0;
        flex-direction: column;
      }
      &__links2{
        margin: 2em auto 0;
        padding: 0;
        border: none;
        height: auto;
      }
      &__link2{
        @include lastM(0 0 1em);
      }
    }
    .menu-nav{
      display: none;
    }
  }
}
