.header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  padding: 1.75em 0;
  color: #fff;
}

.burger{
  display: none;
}

.menu{
  border: 2px solid #FFFFFF;
  border-radius: .625em;
  display: flex;
  &__info{
    display: flex;
    align-items: center;
    padding: 2.375em 1.25em;
    border-right: 2px solid #fff;
    flex-grow: 1;
    max-width: 56.8em;
  }
  &__logo{
    width: 15.1875em;
    display: block;
    margin-right: 3.75em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc{
    max-width: 32em;
  }
}

.menu-nav__link,
.menu-auth{
  text-transform: uppercase;
  font-weight: 600;
  a,
  span{
    font-size: .875em;
    line-height: 140%;
    letter-spacing: 0.05em;
  }
}

.menu-nav{
  flex-grow: 1;
  &__row{
    display: flex;
    height: 50%;
    &:not(:first-child){
      border-top: 2px solid #fff;
    }
  }
  &__link{
    border-right: 2px solid #fff;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 2.5em;
    &._big{
      padding: 0 3.125em;
    }
  }
}

.menu-auth{
  flex-grow: 1;
  background-color: #fff;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  &__link,
  &__split{
    color: $primary;
  }
  &__split{
    margin: 0 .72em;
  }
  &__link{
    &:hover{
      opacity: .8;
    }
  }
}

.lang{
  z-index: 3;
  position: relative;
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  &._active{
    .lang__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    position: relative;
    z-index: 3;
    display: flex;
    align-items: center;
    height: 2em;
    &:hover{
      cursor: pointer;
      path{
        opacity: 1;
      }
    }
    svg{
      width: 1.0625em;
      height: auto;
      margin-left: .625em;
    }
  }
  &__title{
    letter-spacing: 0.15em;
    text-transform: uppercase;
    font-weight: 700;
  }
  &__hidden{
    position: absolute;
    top: 100%;
    left: 0;
    width: calc(100% + 2px);
    overflow: hidden;
    opacity: 0;
    pointer-events: none;
  }
  &__inner{
    border-radius: .25em;
    background-color: #fff;
    box-shadow: 0 .25em .625em rgba(0, 0, 0, 0.09);
    overflow: hidden;
  }
  &__link{
    height: 3.25em;
    display: flex;
    align-items: center;
    padding: 0 1em;
    color: $primary;
    font-weight: 600;
    text-transform: uppercase;
    span{
      font-size: .875em;
    }
    &:hover,
    &._active{
      color: #fff;
      background-color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &._open{
      .burger{
        background-image: url(../img/burger2.svg);
      }
      .menu-nav{
        transform: translateX(0%);
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .burger{
    margin-left: auto;
    display: flex;
    flex-shrink: 0;
    width: 1.75em;
    height: 1.75em;
    position: relative;
    z-index: 11;
    transition: all .0s;
    background-image: url(../img/burger.svg);
    @include bgImgSize(100%);
  }

  .menu{
    border: none;
    &__info{
      padding: 0;
      border: none;
    }
    &__desc{
      display: none;
    }
    &__logo{
      width: 12em;
      margin: 0;
      position: relative;
      z-index: 11;
    }
  }

  .menu-nav{
    font-size: 1.125em;
    position: fixed;
    bottom: 0;
    left: 0;
    overflow-y: auto;
    height: $fullHeight;
    width: 100%;
    background: $black;
    z-index: 10;
    padding: 6em 1.25em 2em;
    margin: 0;
    flex-direction: column;
    justify-content: unset;
    opacity: 0;
    pointer-events: none;
    transform: translateX(-200%);
    transition: all .4s;
    &__row{
      height: unset;
      border: none !important;
      flex-direction: column;
    }
    &__link{
      border: none !important;
      height: unset;
      padding: 0 !important;
      @include lastM(0 0 1.5em);
    }
    .lang{
      order: -1;
      height: unset;
      width: 80%;
      max-width: 20em;
      margin: 0 auto 2em;
    }
  }

  .menu-auth{
    flex-direction: column;
    background-color: transparent;
    &__split{
      display: none;
    }
    &__link{
      @include lastM(0 0 1.5em);
    }
  }
}
