@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-primary{
    display: grid;
    grid-template-columns: calc(100% - 4.375em) .375em 4em;
    align-items: center;
    position: relative;
    &._type2{
        .m-btn-primary__arrow{
            &:before{
                opacity: .5;
            }
        }
    }
    &:hover{
        .m-btn-primary__arrow{
            &:before{
                transform: translateX(0);
            }
        }
        .m-btn-primary__title{
            background-size: 200% auto;
        }
        &:after{
            background-color: #A42FB7;
        }
    }
    &:after{
        content: '';
        display: block;
        height: 1.3125em;
        background-color: $primary;
        order: 2;
        transition: inherit;
    }
    &__title{
        @include center;
        height: 3.75em;
        padding: 0 3.125em;
        background-image: $gradient;
        background-position: center;
        box-shadow: 0 1.875em 3.125em rgba(186, 65, 131, 0.25);
        border-radius: 1.25em;
        font-size: 1em;
        line-height: 140%;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        color: #fff;
        transition: inherit;
        order: 1;
        white-space: nowrap;
    }
    &__arrow{
        order: 3;
        position: relative;
        overflow: hidden;
        border-radius: 1.25em;
        filter: drop-shadow(0 1.875em 3.125em rgba(186, 65, 131, 0.25));
        &:before{
            content: '';
            position: absolute;
            transform: translateX(-120%);
            top: 0;
            right: 0;
            width: 4em;
            height: 3.75em;
            border-radius: inherit;
            background: $gradient;
            transition: all .3s;
        }
    }
    img{
        height: 100%;
        width: 100%;
        position: relative;
        z-index: 2;
    }

}


@media screen and (max-width: 576px){
    .m-btn-primary{
        font-size: .875em;
        width: 100%;
        &__title{
            padding: 0;
        }
    }
}
