@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.grid-column3,
.grid-column4,
.grid-column5{
  display: grid;
  gap: .625em;
}

.grid-column3{
  grid-template-columns: repeat(3,calc(100% / 3 - 1.25em / 3));
}

.grid-column4{
  grid-template-columns: repeat(4,calc(25% - 1.875em / 4));
}

.grid-column5{
  grid-template-columns: repeat(5,calc(20% - 2.5em / 5));
}

@media screen and (max-width: 992px){
  .grid-column3,
  .grid-column4,
  .grid-column5{
    grid-template-columns: repeat(2,calc(50% - .3125em));
  }
}

@media screen and (max-width: 576px){
  .grid-column3,
  .grid-column4,
  .grid-column5{
    grid-template-columns: 100%;
  }
}
