.text50{
  font-size: 3.125em;
  line-height: 140%;
}

.text36{
  font-size: 2.25em;
  line-height: 140%;
}

.text18{
  font-size: 1.125em;
  line-height: 150%;
}

.text16{
  font-size: 1em;
  line-height: 140%;
}

.text14{
  font-size: .875em;
  line-height: 150%;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.gray-text{
  color: $gray;
}


@media screen and (max-width: 992px){
  .text50{
    font-size: 1.875em;
  }

  .text36{
    font-size: 1.625em;
  }

  .text16{
    font-size: .875em;
  }
}
